.title {
    width: 100%;
    text-align: center;
}

.title h1 {
    padding: 20px;
}

.main-box {
    width: 100%;
    text-align: center;
}

form {
    margin-top: 100px;
}

input{
    width: 80%;
    text-align: center;
    padding: 10px 20px;
    font-size: 20px;
    appearance: none;
    background: none;
    border: none;
    outline: none;
    background-color: rgba(164, 196, 255, 0.5);
    border-radius: 16px 16px 16px 16px;
}

.buttons-box {
    width: 100%;
    text-align: center;
    margin-top: 50px;
}

.buttons-box p {
    font-style: italic;
}

.button-api-frame {
    width:100%;
    height:100%;
    border:none;
}

.video-responsive {
    width: 100%;
    height: auto;
    overflow: hidden;
    padding-bottom: 56.25%;
    position: relative;
    height: 0;
    text-align: center;
  }
  
  .video-responsive iframe {
    text-align: center;
    height: 50%;
    width: 50%;
    position: absolute;

  }